import { Component, OnInit } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ActivatedRoute, Router } from '@angular/router';
import { EstadoCitasService } from '../../../providers/Api/EstadoCitas.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-hoja',
  templateUrl: './hoja.page.html',
  styleUrls: ['./hoja.page.scss'],
})
export class HojaPage implements OnInit {

  fotos = [];
  repuestos = [];
  trabajos = [];

  role = '';

  cargaFotos = true;

  id = '';
  idHoja = '';

  estado = '';

  tipo = '';
  placa = '';
  constructor(
    private modalCtrl: ModalController,
    private alertCtrl: AlertController,
    private almacenamiento: InicioService,
    private active: ActivatedRoute,
    private router: Router,
    private hojaApi: EstadoCitasService,
    private toast: ToastServices
  ) { }

  ngOnInit() {
  }

  async ionViewWillEnter() {

    this.tipo = this.active.snapshot.paramMap.get('tipo');

    if (this.active.snapshot.paramMap.get('tipo') === 'hoja') {
      await this.miHoja2();
    } else {
      await this.miHoja();
    }
    
   
  }

  async refrescando(event) {

    if (this.active.snapshot.paramMap.get('tipo') === 'hoja') {
      await this.miHoja2();
    } else {
      await this.miHoja();
    }
    
    event.target.complete();
  }

  tomarFoto() {
    this.router.navigate(['/hoja-vida-modal', this.active.snapshot.paramMap.get('id'), 'fotos', this.idHoja]);
  }

  verFoto(idx) {
    this.router.navigate(['/hoja-vida-modal', this.active.snapshot.paramMap.get('id'), 'ver', this.idHoja, idx]);
  }

  viajar( tipo ) {
    this.router.navigate(['/hoja-vida-modal', this.active.snapshot.paramMap.get('id'), tipo, this.idHoja])
  }

  async miHoja() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.hojaApi.leeHoja(info.token, {id: this.active.snapshot.paramMap.get('id')}).subscribe(async (data: any) => {
        console.log(data);
        if (data.exe) {
          if (data.response) {
            this.idHoja = data.response._id;
          this.repuestos = data.response.repuestos;
          this.trabajos = data.response.trabajos;
          this.estado = data.response.cita.estado;
          }
          await this.traerFotos();
        } else {

        }
      }, (err) => {
        this.toast.toastError('No se pudo traer la información de la hoja de vida, inténtelo más tarde');
      })
    }
  }


  async miHoja2() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.hojaApi.leeHoja2(info.token, {id: this.active.snapshot.paramMap.get('id')}).subscribe(async (data: any) => {
        console.log(data);
        if (data.exe) {
          if (data.response) {
            this.idHoja = data.response._id;
            this.repuestos = data.response.repuestos;
            this.trabajos = data.response.trabajos;

            this.placa = data.response.placa;
          }
          await this.traerFotos();
        } else {

        }
      }, (err) => {
        this.toast.toastError('No se pudo traer la información de la hoja de vida, inténtelo más tarde');
      })
    }
  }


  async traerFotos() {
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
          this.role = info.role;
          this.hojaApi.leefotos(info.token, {id: this.idHoja}).subscribe((data: any) => {
            if (data.exe) {
              this.fotos = data.response;
            } else {

            }
            this.cargaFotos = false;
          }, (err) => {
            this.cargaFotos = false;
            this.toast.toastError('No se pudo traer las fotos de la hoja de vida, inténtelo más tarde');
          })
      }
  }

  async estados() {
    let msg = 'PENDIENTE';

    if (this.estado === 'PENDIENTE') {
      msg = 'Marcar como "en Progeso"';
    } else if (this.estado === 'EN PROGRESO') {
      msg = 'Marcar como "Terminado"';
    }
    const alert = await this.alertCtrl.create({
      header: 'Cambiar Estado de Cita',
      message: `Actualmente la cita esta en estado ${this.estado}`,
      mode: 'ios',
      buttons: [
        {
          text: 'cancelar',
          role: 'cancel',
        },
        {
          text: msg,
          handler: () => {
            this.estado = this.estado === 'PENDIENTE' ? 'EN PROGRESO' : 'TERMINADO';
            this.cambiarEstado();
          }
        }
      ]
    })

    await alert.present();
  }

  async cambiarEstado() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.hojaApi.editarCita(info.token, this.active.snapshot.paramMap.get('id'), {estado: this.estado}).subscribe((data: any) => {
        console.log(data);
        if (data.exe) {
          this.toast.toastExitoso('Estado cambiado');
        } else {
          this.toast.toastError('No se pudo cambiar el estado de la cita, inténtelo ás tarde');
        }
      }, (err) => {
        this.toast.toastError('No se pudo cambiar el estado de la cita, inténtelo ás tarde');
      });
    }
  }

}
